


<template>
   <div>
        <loader :is-visible="isLoading"></loader>
  
    <TgList IsShowAction="true"
        ModuleName="CRM" 
        SubModuleCode="CRM_SALES_ORDERS" 
        IdentityColumn="Order_id"
        :HeaderText="$t('CRM_SALES_ORDERS')"
        :ListData="SalesOrderData" 
        :HeaderData="Headers"
        :HeaderButtons="listheaderbuttons" 
        :TotalRecords="TotalRecords"
        :SearchFields="leftSearchFields"
        :RenderRowActionMethod="renderActions"
        :ListDataCallBackFunction="FetchData"
        :LegendArray="LegendArray"
        :IsFilterApplied="searchCondition?true:false"
        :SortExp="SortExp"
        :SortBy="SortBy"
        :CurrentPageNumber="PageNumber"
        v-on:LeftsearchButtonClick="leftCommonSearch"
        v-on:PagerButtonClick="pagerMethod"
        v-on:HeaderButtonClick="commonHeaderButtonClick"
        v-on:ActionButtonClick="actionButtonClick"
        v-on:SortdataButtonClick="sortdata">
        <template v-slot:slotdata="{data}">
            <template v-if="data.column.COLUMN_NAME == 'status_id'">
                <listing-status :isfor="'CRM_SALES_ORDERS'" v-if="isCRMShowStausDrop(data.row.status_id, 'CRM_SALES_ORDERS')" ref="updatestatus" v-bind:dataid="data.row.Order_id" data-controller="Crm" v-bind:statuslist="StatusListForListing" v-bind:item="data" data-action="index"  v-bind:dataselected="data.row.status_id" v-bind:callbackfunction="FetchData" v-bind:isselectedvaluestring="true"
                >
                </listing-status>
                <a class="inactive-disabled" v-if="!isCRMShowStausDrop(data.row.status_id, 'CRM_VENDORS')">
                    <span v-bind:class="getStatusClass(data.row.status_id, 'status_id')">
                        {{data.row[data.column.COLUMN_NAME]}}
                    </span>
                </a>
            </template>
        </template>
    </TgList>
    <modalfollowupslist 
        :srcId="souceID" 
        v-if="showAddEventModal" 
        modulename="CRM" 
        submodulecode="CRM_SALES_ORDERS" 
        :userid="userid" 
        @close="closeEventViewModal">
    </modalfollowupslist>
    <searchfilter 
        v-if="showFilter" 
        submodulecode="CRM_SALES_ORDERS" 
        modulename="CRM" 
        v-on:close="showFilter = false" 
        v-bind:callbackfunction="FetchData"
        v-on:FilterData="FilterData">
        <h5 slot="header">{{$t('Search')}}</h5>
    </searchfilter>
    <viewlayout 
        v-if="ShowManageView" 
        v-show="ShowManageView" 
        submodulecode="CRM_SALES_ORDERS"
        modulename="CRM"                
        @close="ShowManageView = false"
        v-bind:callbackfunction="FetchData"
        v-bind:commonapiurl="commonAPIUrl"
        :usertype="usertype">
        <h5 slot="header">{{$t('Search')}}</h5>
    </viewlayout>
   </div>
</template>

<script>
  import Schema from "../../CRM/SalesOrder/SalesOrderSchema"    
    import SalesOrderDataService from "../../../services/DataService"
    import  viewlayout from '../../../components/Common/Modal/view-layout.vue'
    import TgList from '../../../components/Common/TgList.vue' 
    import searchfilter from '../../../components/Common/Modal/search-filter.vue'
    import modalfollowupslist from '../../../components/Common/Modal/modalfollowupslist.vue'

export default {
    components: {
        TgList,
        viewlayout,
        searchfilter, 
        modalfollowupslist       
    },
   data() {
            return {
            LegendArray:[],
            isLoading: false,
            SalesOrderData: null,
            txtsalesordernumber: '',
            txtsalesordertitle: '',
            options: null,
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftSearchFields,
            DependentOption: [{               
                useCurrent: false,
                showClear: true,
                showClose: true,
                allowInputToggle: true,
                minDate: new Date(Date.now())
            }
            ],
            headers: null,
            Headers: null,
            appliedFilter: [],
            TotalRecords: 0,
            PageNumber: 1,
            PageSize: 10,
            souceID: 0,
            showAddEventModal: false,
            SortBy: '',
            SortExp: '',
            PageSizeOption: null,
            CurrentPage: 1,
            ShowManageView: false,
            TotalPages: 0,
            GroupStart: 1,
            GroupEnd: 3,
            Status: '',
            SalesOwner: '',
            OrderSubject: '',
            StartDateForSearch: '',
            ExpiryDateForSearch: '',
            DealName: '',
            LeadOwner: '',
            ContactName: '',
            AccountName: '',
            LeadName: '',
            dateFrom: '',
            dateTo: '',
            CreatedBy: '',
            BookName: '',
            SaleOrderDateFrom: '',
            SaleOrderDateTo: '',
            noRecord: false,
            showFilter: false,
            searchCondition: '',
            IsAnySearch: false,
            listActions: Schema.listActions,           
            conditionForInstantSearch: "",
            StatusListForListing: [],          
            userid: null,
            usertype:null,
            privilegeParams: null           
            } 
        },     
        created: function () {
            this.userid = this.GetUserInfo.ID;
            this.usertype = this.GetUserInfo.UserType;
            this.privilegeParams = `controller=CRM&action=`;
            var VueObj = this;
            VueObj.FetchData();
            VueObj.GetPageSizeOption();
            VueObj.getStatuslistForListing("CRM", "en");
            VueObj.LegendArray = [
                {
                    class: 'legend-pending colorboxsquare',
                    text: (VueObj.$t('ListingLegend', ['Sales Order']))
                }
            ]
        },
        methods: { 
            renderActions: function(row) {
                var self = this;
                let rtnActionArray = [];
                self.listActions.forEach(actionItem => {
                    let action = {...actionItem};
                    switch (actionItem.title) {
                        case 'View':
                           action.isVisible=row.status_id!='Draft';
                           break;
                        case 'Edit':
                            action.isVisible = row.ref_source_id<=0 && row.status_id != 'Closed';
                            break;
                        case 'FollowUps':
                            action.isVisible = row.status_id != 'Closed';
                            break;
                        case 'ConvertSalesOrder':
                            action.isVisible = row.status_id!='Draft' && row.ref_source_id<=0 && row.status_id!='Closed';
                            break;                                               
                    }
                    rtnActionArray.push(action);
                });
                return rtnActionArray;
            }, 
            commonHeaderButtonClick: function (event) {            
                switch (event.callbackfunction) {                                     
                    case "ManageView":
                      this.ManageView();
                        break;                                   
                    case "openFilterPopUp":
                        this.openFilterPopUp();
                        break;
                    case "commonDeleteData":
                        this.commonDeleteData();
                        break;  
                    case "removeFilter":
                        var ths = this;
                        this.searchCondition = '';
                        const removeFilterindex= ths.listheaderbuttons.findIndex(object => {
                            return object.callbackfunction === 'removeFilter';
                        });
                        ths.listheaderbuttons[removeFilterindex].isvisible=false;
                        const openFilterPopUpindex= ths.listheaderbuttons.findIndex(object => {
                            return object.callbackfunction === 'openFilterPopUp';
                        });
                        ths.listheaderbuttons[openFilterPopUpindex].iconClass='fa fa-filter';
                        try {
                            ths.appliedFilter = [];
                        } catch (e) { };
                        ths.FetchData();
                        break;  
                        case "setTourGuideSteps":
                    this.setTourGuideSteps(0);
                        break;
                }
            },
            ManageView: function () { 
                this.ShowManageView = true;
                setTimeout(function () {
                    $("#modalManageView").modal('show');
                    setTimeout(function () {
                        SetBootstraptoolTip();
                    }, 200);
                }, 1000);           
            },
            pagerMethod: function (value) {              
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
            },   
            currentPageMethod: function (value) {          
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
            },
            leftCommonSearch: function (value) {
                      
            this.PageNumber = value.PageNumber;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
            },          
            DependentDateObject: function (date) {
                var checkDate = new Date();
                if ((typeof date !== 'undefined') && date != '') {
                    checkDate = new Date(Date.parse(date));
                }
                else {
                    checkDate = new Date(Date.now());
                }
                this.DependentOption[0] = {                    
                    useCurrent: false,
                    showClear: true,
                    showClose: true,
                    allowInputToggle: true,
                    minDate: checkDate
                }
            },
            SetCurrentPage: function (pageNumber) {
                this.PageNumber = pageNumber;
                if (pageNumber > this.GroupEnd || pageNumber == 1) {
                    this.GroupStart = pageNumber;
                    this.GroupEnd = pageNumber + 2;
                }
                else if (pageNumber < this.GroupStart && pageNumber > 1) {
                    this.GroupEnd = this.GroupStart - 1;
                    this.GroupStart = pageNumber - 2;
                }
                this.FetchData();
            },            
            sortdata: function (s) {
                var SortDir = "sort tb_headerSortDown";
                this.SortBy = s;
                    if (s === this.SortBy) {                       

                    if (this.SortExp == "") {
                        this.SortExp = "ASC";
                        SortDir = "sort tb_headerSortDown";
                    }
                    else if (this.SortExp == "ASC") {
                        this.SortExp = "DESC";
                        SortDir = "sort tb_headerSortUp";
                    }
                    else
                    {
                        this.SortExp = "ASC";
                        SortDir = "sort tb_headerSortDown";
                    }
                }               
                this.FetchData();
                $("#" + s+'  span:first-child').attr("class", SortDir);
            },
            SetPageSize: function (event) {
                this.PageSize = event.target.value;
                this.PageNumber = 1;
                this.GroupStart = this.PageNumber;
                this.GroupEnd = this.PageNumber + 2;
                this.FetchData();
            },
            cancelOrRefresh: function () {
                this.SetPageSize = 10;
                this.txtsalesordernumber = '';
                this.txtsalesordertitle = '';
                this.searchCondition = '';
                this.IsAnySearch = false;
                this.StartDateForSearch = '';
                this.ExpiryDateForSearch = '';
                this.SalesOwner = '';
                this.DealName = '';
                this.OrderSubject = '';                
                this.ProductName = '';
                this.FetchData();
            },            
            getStatuslistForListing: function (StatusType, lang) {
                var vm = this;
                var url = "langCode=" + lang + "&statusType=" + StatusType + "&hasglobal=true";
                SalesOrderDataService.statusdropdown(url).then(function (response) {
                    if (response.data.status == "Success") {
                        var json = response.data.result;
                        vm.StatusListForListing = json.data;
                    }
                });
            },
            getRequestParams: function (pageNum, pageSize, sortBy, sortExp, isPaged, searchcondition) {  
                let params = {};
                 if (pageNum) { params["pageNum"] = pageNum; }
                if (searchcondition) { params["searchcondition"] = searchcondition; }
                if (isPaged) { params["isPaged"] = isPaged; }
                if (sortBy) { params["sortBy"] = sortBy; }
                if (sortExp) { params["sortExp"] = sortExp; }
                if (pageSize) { params["pageSize"] = pageSize; }
                return params;
            },
            FilterData:function(pageNum,pageSize,sortBy,sortExp,searchcondition,filterItemData){
               this.pageNum=pageNum;
               this.pageSize=pageSize;
               this.sortBy=sortBy;
               this.sortExp=sortExp;
               this.searchCondition=searchcondition;
               if(typeof filterItemData!=null)
               this.appliedFilter.push(filterItemData);
             this.FetchData();
            },
            FetchData: function () {
                const params = this.getRequestParams(this.pageNumber, this.pageSize, this.sortBy, this.sortExp, this.isPaged, this.searchcondition);                        
                var vm = this;
                vm.isLoading = true;                             
                params.pageSize = vm.PageSize;
                params.pageNumber = vm.PageNumber;
                params.searchCondition = vm.searchCondition;
               if (vm.PageNumber) { params.pageNum = vm.PageNumber; }
                else { vm.PageNumber = params.pageNum = 1; }
                if (typeof vm.isPaged != 'undefined') { params.isPaged = vm.isPaged; }
                else { vm.isPaged = params.isPaged = 1; }
                params.isPaged = vm.isPaged;
                params.sortBy = vm.SortBy;
                params.sortExp = vm.SortExp;             
                var srchCondition = "";
                if (!this.isBlank(this.searchCondition)) { srchCondition = srchCondition + this.searchCondition; }
                if (!this.isBlank(this.conditionForInstantSearch)) {
                    srchCondition = srchCondition + ((!this.isBlank(this.searchCondition)) ? (" AND " + this.conditionForInstantSearch) : this.conditionForInstantSearch);
                }
                if (srchCondition != "") {
                    params.searchCondition = srchCondition; 
                    if(vm.appliedFilter.length>0){
                    const removeFilterindex= vm.listheaderbuttons.findIndex(object => {
                    return object.callbackfunction === 'removeFilter';
                    });          
                    vm.listheaderbuttons[removeFilterindex].isvisible=true;      
                    const openFilterPopUpindex= vm.listheaderbuttons.findIndex(object => {
                    return object.callbackfunction === 'openFilterPopUp';
                    });
                    vm.listheaderbuttons[openFilterPopUpindex].iconClass='fa fa-filter text-success';
            }
                   
                }
                else {
                    params["searchcondition"] = srchCondition;
                if (vm.appliedFilter.length == 0) {

                    const removeFilterindex = vm.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'removeFilter';
                    });
                    vm.listheaderbuttons[removeFilterindex].isvisible = false;
                    const openFilterPopUpindex = vm.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'openFilterPopUp';
                    });
                    vm.listheaderbuttons[openFilterPopUpindex].iconClass = 'fa fa-filter';
                 
                }

            }
                 SalesOrderDataService.SalesOrderListing(params)
                    .then(function (response) { 
                       
                              if (response.data != "" && response.data != null) {
                                  vm.Headers = response.data.schema;
                                  vm.Headers.forEach((column) => {
                                        column.settings = null;
                                        if(column.COLUMN_NAME == 'order_number'){
                                            column.settings = {
                                                clickEvent: vm.clickEventOfColumn,
                                                formatter: vm.columnDataFormatter
                                            }
                                        }
                                        if(column.COLUMN_NAME == 'status_id'){
                                            column.settings = {
                                                isInSlot : true
                                            }
                                        }
                                    });
                                  if (response.data.data.length < vm.PageSize) {                                 
                                      vm.PageSize = (response.data.data.length < 10) ? 10 : response.data.data.length;
                                  }
                                  if (response.data.data.length > 0) {
                                      vm.SalesOrderData = [];
                                      vm.SalesOrderData = response.data.data;
                                      vm.SalesOrderData.forEach((row)=>{
                                        row.isCheckBoxDisabled=false;
                                        row.additionalClass='';
                                        if(row.status_id=='Closed'){
                                      row.isCheckBoxDisabled=true;
                                      row.additionalClass='warning-bar';
                                          }
                                        row.rowDetailsUrl='';
                                        if(row.rowDetailsUrl=='')
                                        {
                                            row.rowDetailsUrl='/CRM/ViewSalesOrderDetails/'+row.Order_id+'/CRM/CRM_SALES_ORDERS';
                                        }
                                    })
                                      vm.TotalRecords = response.data.data[0].total_records;
                                      vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                                      vm.CurrentPage = vm.PageNumber;
                                      vm.noRecord = false;
                                  } else {
                                      vm.noRecord = true;
                                      vm.SalesOrderData = [];
                                      vm.TotalRecords = 0
                                  }
                              }
                              else {
                                  vm.noRecord = true;
                                  vm.noRecord = true;
                                  vm.SalesOrderData = [];
                                  vm.Headers = [];
                                  vm.TotalRecords = 0
                              }                                                      
                              setTimeout(function(){
                                vm.ResponsiveDataTable('tablelistingdata')
                            }, 500);  
                            vm.isLoading = false;                                                  
                          })
            },
            clickEventOfColumn:function(row, column){
                if(column.COLUMN_NAME == 'order_number'){
                    this.$router.push({ path: '/CRM/ViewSalesOrderDetails/'+row.Order_id+'/CRM/CRM_SALES_ORDERS' });
                }
            },
            CheckAllDiabled: function () {
                $('.chkitems').attr("disabled", false);
                if ($('.chkItems').not("[id^='chkAll']").length == $('.chkItems:disabled').not("[id^='chkAll']").length) {
                    $('.chkitems').attr("disabled", true);
                    $('.chkitems').attr("checked", false);
                }
                if ($('.chkItems:checked').not("[id^='chkAll']").length<=0) {
                    $('#DeleteMultiple').addClass('disabled');
                }
            },
            GetPageSizeOption: function () {
                var list = [];
                var defaultPageSize = 10;
                for (var i = defaultPageSize; i < defaultPageSize + 95; i += 5) {
                    list.push(i);
                }
                this.PageSizeOption = list;
            },
            commonDeleteData: function (event) {    
                  var vm = this;
            SalesOrderDataService.CheckPrivilege(vm.privilegeParams + 'DeleteSalesOrder')
                .then(function (response) {
             if (response.data) {
                    var val = "";
                    $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                        if (val.length > 0)
                            val += ',';
                        val += $(this).val();
                    });
                    if (val.length > 0) {
                        vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Sales_Order")), function () {
                            var postJSON = {
                                ids: val,
                                moduleName: "CRM",
                                subModuleCode: "CRM_SALES_ORDERS"
                            };
                            var objectPostString = JSON.stringify(postJSON);
                            SalesOrderDataService.CommonDeleteData(objectPostString).then(function (response) {                               
                                var box = "";
                                if (response.status == 200) {
                                    $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                         $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');                               
                                    vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Sales_Order')]), "success", true, vm.$t("Alert")); 
                                    vm.PageNumber=parseInt(vm.PageNumber)-1;
                                    if(vm.PageNumber<=0){
                                        vm.PageNumber=1;
                                    } 
                                    vm.FetchData();                                    
                                }
                                else {
                                   vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Sales_Order')]), "failure", true, vm.$t("Alert"));
                                }
                              
                               
                            });
                        });
                    }
                    else {
                        alert(vm.$t("Selectanyrecordtodelete"));
                    } 
                }
                else {
                        vm.ShowAlert('You are not authorized', "failure", true, "Alert");
                    }
            });               
            },
            ConvertToOther: function (item) {
                var vm = this;
                vm.confirmR("Are you sure, You want to Convert To Sales Order?", true, "", function () {
                    var postJSON = {
                        SubModuleCode: "CRM_SALES_ORDERS",
                        OrderId: item.Order_id,
                        ToSubModuleCode: 'CRM_INVOICE'
                    }
                    var objectPostString = JSON.stringify(postJSON);
                                         
                    SalesOrderDataService.ConvertOrder(objectPostString).then(function (response) {
                        if (response.data[0].Code == '1') {     
                            vm.FetchData();                            
                            vm.ShowAlert(vm.$t('convert Sales Order Successfully', [vm.$t("Sales_Order")]), "success", true, vm.$t("Alert"));
                        }
                        else {
                            vm.ShowAlert(vm.$t('AddedError', [vm.$t('Sales_Order')]), "failure", true, vm.$t("Alert"));
                            
                        }
                         
                    }, function (response) {
                         
                    });
                });
            },
            openFilterPopUp(source_id) {
                // this.cancelOrRefresh();
                this.showFilter = true;
            },
            openAddNewEvent(item, source_id) { 
                var vm = this;
                vm.souceID = source_id.Order_id;
                vm.selectedItem = item;
                vm.showAddEventModal = (vm.showAddEventModal) ? false : true;                              
            },
            closeEventViewModal: function () {                       
                this.showAddEventModal = false;
            },
            actionButtonClick:function(callbackfunction,item,index,event){                          
               var vm=this;
               switch(callbackfunction){                
                     case 'checkPrivilegeView':
                        break;
                    case 'checkPrivilegeDetails':
                        break;
                    case 'openNoteListPopUp':
                        vm.openNoteListPopUp(item, index);
                        break;
                    case 'openAddNewEvent':
                        vm.openAddNewEvent(item.Order_id, item);
                        break;                     
                    case 'ConvertToOther':
                        vm.ConvertToOther(item);                                                            
                        break;                    
               }
            }
        },   
        updated: function () {
            this.BindActionButtonEvent();
        },
}
</script>
